import React, { Component } from 'react'
import { Query } from 'react-apollo';
import Error from './ErrorMessage';

const Permissions = props => (
    <Query query={ALL_USERS_QUERY}>
        {({ data, loading, error }) => (
            <>
                <Error error={error} />
                <p>Hello</p>
            </>
        )}
    </Query>
)

export default Permissions;